import React, { Fragment, useState, useCallback } from 'react'
import { TextField, Layout, Form, FormLayout } from '@shopify/polaris';

const Question = () => {

    const [options, setOptions] = useState(1);
    const [question, setQuestion] = useState('');

    const handleOptions = useCallback((option) => setOptions(option), []);
    const handleQuestion = useCallback((question) => setQuestion(question), []);

    const [url, setUrl] = useState('');

    const handleSubmit = useCallback((_event) => setUrl(''), []);

    const handleUrlChange = useCallback((value) => setUrl(value), []);


    return (
        <Fragment>
            <Form noValidate onSubmit={handleSubmit}>
                <FormLayout>

                    <TextField
                        value={question}
                        onChange={handleQuestion}
                        label="Question"
                        type="text"
                        placeholder="Which of the following do you prefer, etc"
                    />
                    <TextField
                        label="Number of options"
                        type="number"
                        max="4"
                        min="1"
                        value={options}
                        onChange={handleOptions}

                    />
                </FormLayout>
            </Form>

        </Fragment >
    )
}

export default Question;